import moment from "moment";
Vue.filter('moment', function(value) {
    if (!value) return "";
    return moment(value.toDate()).format("YYYY-MM-DD hh:mm:ss");
});
Vue.prototype.moment_format = (value) => moment(value.toDate()).format("YYYY-MMM-DD hh:mm:ss");
Vue.prototype.$moment = moment;


Vue.filter('state_filter', function(value) {
    if (value == "online") return "En Ligne";
    if (value == "assigned") return "En Attente";
    if (value == "picked") return "Ramassagé";
    if (value == "road") return "En Route";
    if (value == "delivred") return "Livré";
    return value
});

