import config from './index.js';
import algeria_cities from "../assets/algeria_cities.js";
//
import { FF_FieldValue } from "../firebase";
import faker from "faker";
import moment from "moment";

let fake_phone_number = () => {
    let phone = "+213" + (Math.floor(Math.random() * 300000000) + 500000000);
    return phone // faker.phone.phoneNumber()
}

let request = () => {
    let date = FF_FieldValue.serverTimestamp();
    let phone = fake_phone_number();
    let obj = {
        userPhone: phone,
        //
        description: faker.lorem.sentence(3),
        packageType: config.packageTypes[
            Math.floor(Math.random() * (config.packageTypes.length - 1)) + 1
        ],
        //
        wilayaFrom: "Alger", // faker.address.state()
        wilayaTo: "Alger",
        baladiaFrom: algeria_cities[16].cities[
            Math.floor(Math.random() * algeria_cities[16].cities.length)
        ].value,
        baladiaTo: algeria_cities[16].cities[
            Math.floor(Math.random() * algeria_cities[16].cities.length)
        ].value,
        startLat: faker.address.latitude(),
        startLng: faker.address.longitude(),
        lat_lanFrom: {
            lat: faker.address.latitude(),
            lan: faker.address.longitude()
        },
        endLat: faker.address.latitude(),
        endLng: faker.address.longitude(),
        lat_lanTo: {
            lat: faker.address.latitude(),
            lan: faker.address.longitude()
        },
        //
        weight: Math.floor(Math.random() * 10) + 1,
        width: Math.floor(Math.random() * 10) + 1,
        length: Math.floor(Math.random() * 10) + 1,
        height: Math.floor(Math.random() * 10) + 1,
        //
        driverPhone: fake_phone_number(),
        packageCode: "W" + moment().format("DDMMYY") + "-" + Math.floor(Math.random() * 9998) + 1,
        pickupCode: "" + Math.floor(Math.random() * 9998) + 1,
        secretCode: "" + Math.floor(Math.random() * 9998) + 1,
        //
        arriveName: faker.name.firstName(),
        arriveNumber: fake_phone_number(),
        //
        state: config.requestStates[
            Math.floor(Math.random() * config.requestStates.length)
        ],
        deliveryTime: faker.date.past(),
        createDate: faker.date.past(),
        //
        created_at: date,
        modified_at: date
    };
    return obj;
};

let user = () => {
    let date = FF_FieldValue.serverTimestamp();
    let phone = fake_phone_number();
    let obj = {
        phone: phone,
        //
        name: faker.name.firstName(),
        family: faker.name.lastName(),
        birthday: faker.date.past(),
        genre: config.genres[
            Math.floor(Math.random() * config.genres.length)
        ],
        //wilaya: faker.address.state(),
        //baladia: faker.address.city(),
        wilaya: "Alger",
        baladia: algeria_cities[16].cities[
            Math.floor(Math.random() * algeria_cities[16].cities.length)
        ].value,
        address: faker.lorem.sentence(3),
        lat_lan: {
            lat: faker.address.latitude(),
            lan: faker.address.longitude()
        },
        email: faker.internet.email(),
        //
        kind: config.kinds[
            Math.floor(Math.random() * config.kinds.length)
        ],
        vehicule: config.vehicules[
            Math.floor(Math.random() * config.vehicules.length)
        ],
        //
        type: config.types[
            Math.floor(Math.random() * (config.types.length - 1)) + 1
        ],
        rank: config.ranks[
            Math.floor(Math.random() * config.ranks.length)
        ],
        //
        created_at: date,
        modified_at: date
    };
    return obj;
};


export default { request, user, fake_phone_number };