import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
import { db } from "../firebase";


export default new Vuex.Store({
    state: {
        sign_up_lock: false,
        user: null, // { uid,email, displayName }
        profile: null, // { uid,email, role, profile_driver, profile_driver.first_name, profile_client, profile_client.first_name, ...etc}
        drivers: []
    },
    getters: {
        displayName: state => {
            if (!state.user || !state.profile)
                return null;
            return state.profile.name || state.profile.displayName || state.profile.phone || "X";
            /*
            let displayName = state.user.email;
            let n = displayName.indexOf('@');
            displayName = displayName.substring(0, n != -1 ? n : displayName.length);
            return displayName;*/
        },
    },
    mutations: {
        set_sign_up_lock(state, sign_up_lock) {
            console.log('set_sign_up_lock', sign_up_lock);
            state.sign_up_lock = sign_up_lock;
        },
        init_user_profile(state) {
            console.log("init_user_profile")
            let user = localStorage.getItem('vuex-user');
            let profile = localStorage.getItem('vuex-profile');
            let drivers = localStorage.getItem('vuex-drivers');
            //
            user = JSON.parse(user);
            state.user = user.phoneNumber ? user : null
                //
            profile = JSON.parse(profile);
            state.profile = profile.phone ? profile : null
                //
            drivers = JSON.parse(drivers);
            state.drivers = drivers || null;
        },
        set_user(state, user) {
            console.log('set_user', user);
            localStorage.setItem('vuex-user', JSON.stringify(user));
            state.user = user;
        },
        set_profile(state, profile) {
            console.log('set_profile', profile);
            localStorage.setItem('vuex-profile', JSON.stringify(profile));
            state.profile = profile;
        },
        set_drivers(state, drivers) {
            console.log('set_drivers', drivers);
            localStorage.setItem('vuex-drivers', JSON.stringify(drivers));
            state.drivers = drivers;
        },
    },
    actions: {
        async getdrivers(context) {
            db.collection("configuration").doc("drivers").get()
                .then((doc) => {
                    this.isLoading = false;
                    this.errorMessage = null;
                    console.log("Store : Successfully loaded!");
                    context.commit('set_drivers', doc.data());
                })
                .catch(error => {
                    this.isLoading = false;
                    this.errorMessage = null;
                    console.log("error", error);
                });
        },
        async update_my_profile(context) {
            db.collection("users").doc(context.state.user.phoneNumber).get()
                .then((doc) => {
                    this.isLoading = false;
                    this.errorMessage = null;
                    console.log("Store : Successfully loaded!");
                    context.commit('set_profile', doc.data());
                })
                .catch(error => {
                    this.isLoading = false;
                    this.errorMessage = null;
                    console.log("error", error);
                });
        },
    }
});