<template>
  <div class="input-group">
    <span class="input-group-text" @click="remove_lat_lan">
      <i class="bi-geo-alt" />
    </span>
    <input type="text" class="form-control" readonly :value="text" />
    <span class="input-group-text" @click="get_lat_lan">
      <i class="bi-geo-alt-fill" />
    </span>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      input: {
        lat: "",
        lan: ""
      }
    };
  },
  props: {
    value: {
      type: Object,
      required: true
    }
  },
  computed: {
    text() {
      return this.input.lat != "" && this.input.lan != ""
        ? this.input.lat + "," + this.input.lan
        : "/";
    }
  },
  watch: {
    input(newVal) {
      this.$emit("input", newVal);
    }
  },
  created() {
    if (this.value && this.value.lat != "" && this.value.lan != "")
      this.input = this.value;
  },
  methods: {
    remove_lat_lan() {
      this.input = {
        lat: "",
        lan: ""
      };
    },
    get_lat_lan() {
      if (!navigator.geolocation) {
        this.$swal("Geolocation is not supported by this browser.");
        return;
      }
      navigator.geolocation.getCurrentPosition(position => {
        this.input = {
          lat: position.coords.latitude,
          lan: position.coords.longitude
        };
      });
    }
  }
};
</script>

