<template>
  <div>
    <span v-for="option in options_fixed" :key="option.value" class="mx-2">
      <input
        type="radio"
        class="form-check-input"
        :id="'radio-' + option.value"
        :value="option.value"
        :name="name"
        v-model="model"
      />
      <label :for="'radio-' + option.value" class="form-check-label ms-2">{{option.text}}</label>
    </span>
  </div>
</template>

<script>
export default {
  name: "radioComponent",
  components: {},
  data() {
    return {};
  },
  props: {
    options: {
      type: Array,
      required: true
    },
    value: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: false
    }
  },
  computed: {
    options_fixed() {
      if (typeof this.options[0] == "string") {
        return this.options.map(option => {
          return {
            value: option,
            text: option
          };
        });
      }
      return this.options;
    },
    model: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      }
    }
  },
  watch: {},
  created() {},
  methods: {}
};
</script>

