<template>
  <a
    class="nav-link"
    :class="{'text-warning': mode == 'on','text-secondary': mode == 'off'}"
    type="button"
    @click="switchMode"
  >
    {{mode.toUpperCase()}}
    <i class="bi-geo-alt-fill" />
  </a>
</template>

<script>
import { dbRT } from "../firebase";

export default {
  data() {
    return {
      isGeolocation: false,
      mode: "off",
      driverLat: "",
      driverLng: "",
      modified_at: "",
      watchID: null
    };
  },
  created() {
    if (navigator.geolocation) {
      this.isGeolocation = true;
      this.syncState();
    } else {
      this.isGeolocation = false;
      console.log("Geolocation is not supported by your browser");
    }
  },
  methods: {
    now() {
      return this.$moment().format("YYYY-MMM-DD hh:mm:ss");
    },
    syncState() {
      if (this.$store.state.profile && this.$store.state.profile.phone)
        dbRT
          .ref("users/" + this.$store.state.profile.phone)
          .on("value", snapshot => {
            console.log("users/" + this.$store.state.profile.phone, snapshot);
            if (!snapshot.exists()) {
              console.log("!snapshot.exists()");
              this.mode = "off";
              this.driverLat = "";
              this.driverLng = "";
              this.modified_at = "";
              navigator.geolocation.clearWatch(this.watchID);
              return;
            }
            console.log("snapshot.val()", snapshot.val());
            this.mode = snapshot.val().mode || "off";
            this.driverLat = snapshot.val().driverLat || "";
            this.driverLng = snapshot.val().driverLng || "";
            this.modified_at = snapshot.val().modified_at || "";
            if (this.mode == "on" && this.watchID == null) this.watchPosition();
          });
    },
    switchMode() {
      if (this.mode == "on") this.stopFollowUp();
      else if (this.mode == "off") this.watchPosition();
    },
    stopFollowUp() {
      if (this.watchID) navigator.geolocation.clearWatch(this.watchID);
      if (this.$store.state.profile && this.$store.state.profile.phone)
        dbRT.ref("users/" + this.$store.state.profile.phone).set({
          driverLat: "",
          driverLng: "",
          mode: "off",
          modified_at: this.now()
        });
    },
    watchPosition() {
      if (this.watchID) navigator.geolocation.clearWatch(this.watchID);
      this.watchID = navigator.geolocation.watchPosition(
        this.watchPosition_success,
        this.watchPosition_fail
      );
    },
    watchPosition_success(position) {
      console.log("watchPosition_success(position)", position);
      if (
        this.driverLat == position.coords.latitude &&
        this.driverLng == position.coords.longitude &&
        this.mode == "on"
      ) {
        console.log("It is the same position and mode is on", position);
        return;
      }
      if (this.$store.state.profile && this.$store.state.profile.phone)
        dbRT.ref("users/" + this.$store.state.profile.phone).set({
          driverLat: position.coords.latitude,
          driverLng: position.coords.longitude,
          mode: "on",
          modified_at: this.now() || ""
        });
    },
    watchPosition_fail() {
      if (this.watchID) navigator.geolocation.clearWatch(this.watchID);
      this.$swal("Unable to retrieve your location.");
      console.log("Unable to retrieve your location.");
      this.stopFollowUp();
    }
  }
};
</script>

<style >
.package-track {
  color: #000000;
}
</style>
