import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [{
        path: '',
        name: 'home',
        component: () =>
            import ('../components/homeComponent')
    }, {
        path: '/count-down',
        name: 'count-down',
        component: () =>
            import ('../components/countDownComponent')
    },
    /* DOCUMENT CRUD */
    {
        path: '/documents-index',
        name: 'documents-index',
        component: () =>
            import ('../components/documents/indexComponent')
    }, {
        path: '/documents-create',
        name: 'documents-create',
        component: () =>
            import ('../components/documents/createComponent')
    }, {
        path: '/documents-show/:id',
        name: 'documents-show',
        component: () =>
            import ('../components/documents/showComponent')
    }, {
        path: '/documents-edit/:id',
        name: 'documents-edit',
        component: () =>
            import ('../components/documents/editComponent')
    },
    /* POST CRUD */
    {
        path: '/posts-index',
        name: 'posts-index',
        component: () =>
            import ('../components/posts/indexComponent')
    }, {
        path: '/posts-create',
        name: 'posts-create',
        component: () =>
            import ('../components/posts/createComponent')
    }, {
        path: '/posts-show/:id',
        name: 'posts-show',
        component: () =>
            import ('../components/posts/showComponent')
    }, {
        path: '/posts-edit/:id',
        name: 'posts-edit',
        component: () =>
            import ('../components/posts/editComponent')
    },
    /* -- */
    {
        path: '/packages-index',
        name: 'packages-index',
        component: () =>
            import ('../components/packages/indexComponent')
    },
    {
        path: '/packages-index2',
        name: 'packages-index2',
        component: () =>
            import ('../components/packages/index2Component')
    }, {
        path: '/packages-create',
        name: 'packages-create',
        component: () =>
            import ('../components/packages/createComponent')
    }, {
        path: '/packages-show/:id',
        name: 'packages-show',
        component: () =>
            import ('../components/packages/showComponent')
    }, {
        path: '/packages-edit/:id',
        name: 'packages-edit',
        component: () =>
            import ('../components/packages/editComponent')
    },
    {
        path: '/page-viewer/:file',
        name: 'page-viewer',
        component: () =>
            import ('../components/pageViewerComponent')
    },
    {
        path: '/users-index',
        name: 'users-index',
        component: () =>
            import ('../components/usersComponent')
    },
    {
        path: '/sign-in-phone/:type?',
        name: 'sign-in-phone',
        component: () =>
            import ('../components/signInPhoneComponent')
    }, {
        path: '/sign-out',
        name: 'sign-out',
        component: () =>
            import ('../components/signOutComponent')
    }, {
        path: '/show-profile/:phone',
        name: 'show-profile',
        component: () =>
            import ('../components/showProfileComponent')
    }, {
        path: '/edit-profile/:phone',
        name: 'edit-profile',
        component: () =>
            import ('../components/editProfileComponent')
    },
    /* OTHER */
    {
        path: '/test',
        name: 'test',
        component: () =>
            import ('../components/realTimeComponent')
    },
    {
        path: '/driver-selection',
        name: 'driver-selection',
        component: () =>
            import ('../components/driverSelection/driverSelectionComponent')
    },
    {
        path: '/configuration',
        name: 'configuration',
        component: () =>
            import ('../components/configurationComponent')
    },
    {
        path: '/csv-export',
        name: 'csv-export',
        component: () =>
            import ('../components/csvExportComponent')
    }
]

const router = new VueRouter({
    //mode: 'history',
    base: process.env.BASE_URL,
    routes,
    scrollBehavior(to /*, from, savedPosition*/ ) {
        if (to && to.name == "home" && to.hash) {
            let id = to.hash.substring(1);
            if (id) {
                let homeSection = document.getElementById(id)
                if (homeSection && id)
                    homeSection.scrollIntoView(true);
            }
        } else {
            window.scrollTo(0, 0);
        }
        setTimeout(() => {
            let button = document.getElementById('navbar-toggler');
            if (button.getAttribute('aria-expanded') == "true") button.click();
        }, 50);
    }
})

export default router